// intro
.intro {
	padding: 80px 0 60px;
	background: linear-gradient(270deg, #1a5d5a 0%, #33827f 28.66%, #1a5d5a 100%);
	min-height: calc(100vh - 120px);
	display: flex;
	position: relative;

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		gap: 60px;
		min-height: 100%;
		position: relative;
		z-index: 3;
	}

	&__main {
		color: var(--belyy);
		max-width: 760px;
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;
		z-index: 3;
	}

	&__subtitle {
		font-size: 40px;
		line-height: 1.1;
		margin-bottom: 40px;
	}

	&__title {
		color: var(--belyy);
	}

	&__inner {
		display: flex;
		align-items: center;
		gap: 20px;
		&-text {
			max-width: 250px;
			color: var(--osnovnye-kdk-k-svetlo-zelenyy);
		}
	}

	&__photos {
		display: flex;
		margin-left: 10px;
	}

	&__photo {
		border-radius: 40px;
		width: 70px;
		height: 70px;
		box-shadow: -3px 0 34px 0 rgba(0, 0, 0, 0.15);
		background: var(--osnovnye-kdk-k-svetlo-zelenyy);
		overflow: hidden;
		margin-left: -10px;
		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: top;
		}
	}

	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: end;
		gap: 60px;
		position: relative;
		z-index: 3;
	}

	&__button {
		width: 100%;
		max-width: 420px;
		& button {
			width: 100%;
		}
	}

	&__button-text {
		font-size: 18px;
		line-height: 1.3;
		color: var(--belyy);
		margin-bottom: 20px;
	}

	&__items {
		max-width: 930px;
		max-width: 60%;
		& ul {
			display: flex;
			gap: 12px;
		}

		& li {
			padding: 20px 28px 20px 24px;
			border-radius: 16px;
			backdrop-filter: blur(15px);
			background: rgba(26, 93, 90, 0.6);
			color: var(--belyy);
			display: flex;
			align-items: center;
			gap: 24px;

			&::before {
				content: "";
				display: block;
				width: 10px;
				height: 10px;
				border-radius: 100%;
				background: var(--osnovnye-kdk-osnovnoy-500);
				flex-shrink: 0;
			}
		}
	}

	&__bg {
		position: absolute;
		bottom: 0;
		right: 0;
		max-width: 70%;
		max-height: 98%;
		width: 100%;
		height: 100%;

		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: top left;
		}
	}

	&__pic {
		position: absolute;
		bottom: 0;
		right: 0;
		max-width: 60%;
		max-height: 90%;

		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: top left;
		}
	}
}

.intro-page {
	&__main {
		max-width: 1306px;
		justify-content: start;
	}

	& .intro__title {
		margin-bottom: 60px;
	}

	& .intro__items {
		max-width: none;
		& li {
			padding: 24px 24px 18px;
			background: #306d6a;
			flex-direction: column;
			align-items: stretch;
			gap: 62px;

			&::before {
				margin-left: auto;
			}
		}
	}

	&__bg {
		max-width: 40%;
	}
}

.intro-team {
	&__main {
		max-width: 1024px;
		justify-content: start;
	}

	&__text {
		max-width: 680px;
		opacity: 0.8;
	}

	&__pic {
		bottom: -60px;
		right: 10px;
		max-width: 46%;
		max-height: 90%;
	}
}

.intro-contact {
	min-height: auto !important;
	padding: 80px 0;
	& .intro__title {
		margin-bottom: 60px;
	}

	&__bg {
		right: -35%;
		bottom: -20%;
	}
}

@media (max-width: 1200px) {
	.intro {
		min-height: calc(100vh - 112px);
		&__items {
			display: none;
		}
		&__pic {
			right: -20px;
			max-width: 67%;
		}
	}
	.intro-team {
		&__pic {
			bottom: -60px;
			right: -10px;
			max-width: 46%;
		}
	}
}
@media (max-width: 1024px) {
	.intro {
		padding: 80px 0 0;
		min-height: auto;
		display: block;
		position: relative;

		&__content {
			gap: 50px;
			margin-bottom: 55px;
		}

		&__main {
			max-width: 100%;
		}

		&__subtitle {
			font-size: 30px;
			margin-bottom: 30px;
		}

		&__bg {
			max-width: 110%;
			max-height: 65%;
			width: 110%;
			height: 100%;

			& img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: top center;
			}
		}

		&__pic {
			position: relative;
			display: block;
			width: calc(100% + 60px);
			max-width: calc(100% + 60px);
			margin-left: -30px;
			max-height: 90%;
			z-index: 2;
			right: 0;
		}
	}

	.intro-page {
		&__bg {
			max-width: 40%;
			right: -10%;
			bottom: -40px;
			min-width: 260px;
			& img {
				object-position: top left;
			}
		}
	}

	.intro-team {
		&__pic {
			width: 100%;
			max-width: 60%;
			margin: 0 auto;
		}
	}

	.intro-contact {
		padding: 30px 0;
		& .intro__title {
			margin-bottom: 24px;
		}
		& .intro__content {
			margin: 0;
		}

		&__bg {
			right: -35%;
			bottom: 0;
			width: 70%;
			& img {
				object-position: top left;
			}
		}
	}
}
@media (max-width: 768px) {
	.intro {
		padding: 50px 0 0;

		&__subtitle {
			font-size: 20px;
			margin-bottom: 20px;
		}

		&__inner {
			flex-direction: column;
			align-items: stretch;
			&-text {
				max-width: 200px;
			}
		}

		&__button {
			width: 100%;
			max-width: 100%;
		}

		&__button-text {
			font-size: 16px;
			margin-bottom: 16px;
		}

		&__bg {
			max-height: 60%;
		}

		&__pic {
			width: calc(100% + 70px);
			max-width: calc(100% + 70px);
			margin-left: -50px;
		}
	}

	.intro-page {
		& .intro__title {
			margin-bottom: 40px;
		}
		& .intro__button-text {
			display: none;
		}

		& .intro__bg {
			max-width: 40%;
		}
	}

	.intro-team {
		&__pic {
			position: absolute;
			bottom: -20px;
			right: auto;
			left: 50%;
			transform: translate(-50%, 0%);

			width: 100%;
			max-width: 300px;
			margin: 0 auto;
		}

		&__content {
			margin-bottom: 20px;
			gap: 252px;
		}
	}

	.intro-contact {
		padding: 30px 0;
		& .intro__title {
			margin-bottom: 24px;
		}
		& .intro__content {
			margin: 0;
		}

		&__bg {
			width: 90%;
			right: -45%;
			bottom: -12%;
		}
	}
}
