/* Общие стили */
html.lock {
	overflow: hidden;
}
html.burger-lock {
	overflow: hidden;
}
html.burger-lock::before {
	//opacity: 1;
}
html::before {
	display: block;
	content: "";
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.3215686275);
	position: fixed;
	inset: 0;
	opacity: 0;
	z-index: 10;
	pointer-events: none;
	-webkit-transition: opacity 0.3s linear;
	-o-transition: opacity 0.3s linear;
	transition: opacity 0.3s linear;
}
.no-scroll {
	overflow: hidden;
}
body.lock {
	overflow: hidden;
}
body.burger-lock {
	overflow: hidden;
}

body {
	margin: 0;
	overflow-x: hidden;
	overflow-y: auto;
	padding: 0;
	background-color: #fff;
	font-family: var(--font-family);
	font-weight: 400;
	font-size: 18px;
	line-height: 1.3;
	color: #686d79;
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	padding: 0 30px;
	max-width: var(--container-width);
	width: 100%;
}

// sections
section {
	overflow: hidden;
}
.section-offset {
	margin-bottom: 160px;
}
.section__top {
	display: flex;
	//align-items: center;
	justify-content: space-between;
	gap: 20px;
	margin-bottom: 32px;
	padding-top: 20px;
	border-top: 1px solid #c8d3cf;
	font-size: 16px;
	line-height: 1.3;
	text-transform: uppercase;
	color: #8f9c96;
}

.green-block {
	padding: 160px 0;
	background: var(--osnovnye-kdk-k-zelenyy);
	color: var(--belyy);
	& .section__top {
		border-color: rgba(255, 255, 255, 0.2);
		color: #aabeb5;
	}
	& .title-h2 {
		color: var(--belyy);
	}
}

@media (max-width: 1200px) {
	.section-offset {
		margin-bottom: 120px;
	}
	.section__top {
		margin-bottom: 40px;
	}
	.section-padding {
		padding: 60px 0;
	}
	.swiper {
		overflow: visible;
	}
	.green-block {
		padding: 120px 0;
	}
}
@media (max-width: 768px) {
	.section-offset {
		margin-bottom: 80px;
	}
	.container {
		padding: 0 var(--container-padding);
	}
	.section__top {
		padding-top: 8px;
		font-size: 12px;
		margin-bottom: 20px;
		& img {
			max-height: 8px;
			object-fit: contain;
		}
	}
	body {
		font-size: 16px;
	}

	.green-block {
		padding: 80px 0;
	}
}

// titles
.title-h1,
.title-h2 {
	font-family: var(--font-family);
	font-weight: 500;
	line-height: 1.1;
	color: #30423c;
}
.title-h1 {
	font-size: 92px;
	color: var(--belyy);
}
.title-h2 {
	font-size: 60px;
}

.section__title {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	gap: 40px;
	margin-bottom: 60px;
	& .title-h2 {
		max-width: 1005px;
	}
}

@media (max-width: 1300px) {
	.title-h1 {
		font-size: 64px;
	}
	.title-h2 {
		font-size: 50px;
	}
	.section__title {
		margin-bottom: 50px;
	}
}
@media (max-width: 768px) {
	.title-h1 {
		font-size: 38px;
	}
	.title-h2 {
		font-size: 30px;
	}
	.section__title {
		margin-bottom: 30px;
	}
}

// logo
.logo {
	& img {
		max-width: 186px;
		max-height: 40px;
		min-width: 148px;
		min-height: 32px;
		object-fit: contain;
	}
}

@media (max-width: 1400px) {
	.logo {
		gap: 9px;
		&__picture {
			& img {
				width: 46px;
				height: 46px;
			}
		}

		&__text {
			& p {
				font-size: 28px;
				margin-bottom: 2px;
			}
			& span {
				font-size: 14px;
			}
		}
	}
}
@media (max-width: 768px) {
	.logo {
		&__text {
			& p {
				font-size: 22px;
			}
			& span {
				font-size: 10px;
			}
		}
	}
}

//  buttons
.primary-btn,
.secondary-btn {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 12px;
	border-radius: 16px;
	padding: 32px;
	background: var(--belyy);
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 20px;
	line-height: 1.3;
	text-transform: uppercase;
	color: #30423c;
	transition: all 0.2s ease-in;

	&::after {
		content: "";
		display: inline-block;
		width: 24px;
		height: 24px;
		background-color: #f04a25;
		-webkit-mask-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.515 8.24208L5.91752 19.8396L4.16039 18.0825L15.7579 6.48496H4L5.38053 4L20 4V18.6195L17.515 20V8.24208Z' fill='black'/%3E%3C/svg%3E");
		mask-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.515 8.24208L5.91752 19.8396L4.16039 18.0825L15.7579 6.48496H4L5.38053 4L20 4V18.6195L17.515 20V8.24208Z' fill='black'/%3E%3C/svg%3E");
		-webkit-mask-repeat: no-repeat;
		mask-repeat: no-repeat;
		-webkit-mask-position: center;
		mask-position: center;
		-webkit-mask-size: contain;
		mask-size: contain;
		transition: all 0.3s ease;
	}

	&.small-btn {
		border-radius: 10px;
		padding: 24px;
		font-size: 16px;
		background: var(--osnovnye-kdk-k-zelenovatyy);
		color: var(--belyy);
		&::after {
			background-color: var(--belyy);
		}
	}
}
.primary-btn {
	&:hover {
		background: rgba(255, 255, 255, 0.9);
		&::after {
			transform: rotate(45deg);
		}
	}
}
.secondary-btn {
	&:hover {
		background: var(--osnovnye-kdk-k-zelenyy);
		color: var(--belyy);
		&::after {
			background: var(--belyy);
			transform: rotate(45deg);
		}
	}
}
.small-btn:hover {
	background: var(--belyy);
	color: var(--tekst-osnovnoy-tekst);
	&::after {
		background-color: var(--osnovnye-kdk-k-oranzhevyy);
	}
}

.btn-simple {
	display: block;
	border-radius: 10px;
	padding-block: 15px;
	padding-inline: 18px;
	background: var(--osnovnye-kdk-k-zelenyy);
	font-weight: 500;
	font-size: 16px;
	line-height: 1.3;
	text-transform: uppercase;
	color: var(--belyy);
	text-align: center;
	transition: all 0.3s ease;
	&:hover {
		background: rgba(26, 93, 90, 0.9);
	}

	&.white {
		border: 1px solid rgba(26, 93, 90, 0.5);
		background: transparent;
		color: var(--osnovnye-kdk-k-zelenyy);
		&:hover {
			color: var(--belyy);
			background: var(--osnovnye-kdk-k-zelenyy);
		}
	}
}

.btn-show {
	display: block;
	border: 1px solid rgba(26, 93, 90, 0.5);
	border-radius: 10px;
	padding: 15px 24px;
	font-weight: 500;
	font-size: 16px;
	line-height: 1.3;
	text-transform: uppercase;
	color: var(--osnovnye-kdk-k-zelenyy);
	width: fit-content;
	transition: all 0.3s ease;
	&:hover {
		border: 1px solid var(--osnovnye-kdk-k-zelenyy);
		color: var(--osnovnye-kdk-k-zelenyy);
	}
}

@media (max-width: 768px) {
	.primary-btn,
	.secondary-btn {
		border-radius: 12px;
		padding: 20px;
		font-size: 14px;

		&::after {
			width: 18px;
			height: 18px;
		}

		&.small-btn {
			padding: 19px 18px;
			font-size: 14px;
			&::after {
				width: 12px;
				height: 12px;
			}
		}
	}

	.btn-simple {
		padding-block: 14px;
		padding-inline: 16px;
		font-size: 14px;
	}

	.btn-show {
		padding: 16px 20px;
		font-size: 14px;
		width: 100%;
	}
}

// networks
.networks {
	display: flex;
	flex-direction: column;
	gap: 12px;
}
.network {
	border-radius: 20px;
	padding: 8px;
	background: #e9f1ef;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 16px;
	font-weight: 500;
	font-size: 20px;
	line-height: 1.3;
	text-transform: uppercase;
	color: var(--tekst-osnovnoy-tekst);
	max-width: 360px;
	transition: all 0.3s ease;

	&-icon {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 12px;
		background: var(--belyy);
		width: 60px;
		aspect-ratio: 1 / 1;
		flex-shrink: 0;
	}

	&-name {
		flex-grow: 1;
	}

	&-num {
		border: 1px solid rgba(26, 93, 90, 0.2);
		border-radius: 10px;
		padding: 21px 9px;
		font-size: 14px;
		color: var(--osnovnye-kdk-k-zelenyy);
	}

	&:hover {
		background: var(--osnovnye-kdk-k-zelenyy);
		color: var(--belyy);
		& .network-num {
			border-color: rgba(255, 255, 255, 0.2);
			color: var(--belyy);
		}
	}

	&:active {
		background: rgba(26, 93, 90, 0.9);
	}
}

.network.light {
	background: rgba(255, 255, 255, 0.15);
	color: var(--belyy);
	& .network-num {
		border: 1px solid rgba(255, 255, 255, 0.2);
		color: var(--belyy);
	}
	&:hover {
		background: var(--osnovnye-kdk-k-zelenyy);
	}
}

@media (max-width: 768px) {
	.networks {
		gap: 8px;
	}
	.network {
		border-radius: 15px;
		gap: 12px;
		font-size: 16px;
		max-width: 100%;

		&-icon {
			border-radius: 7px;
			width: 48px;
		}

		&-num {
			border-radius: 7px;
			padding: 16px 8px;
			font-size: 12px;
		}
	}
}



/*  form  */
input,
textarea {
	border-radius: 10px;
	background: var(--belyy);
	padding: 20px 24px;
	font-size: 18px;
	color: var(--tekst-osnovnoy-tekst);
	border: 1px solid transparent;
	transition: all 0.3s ease;
	&::placeholder {
		color: #686d79;
	}
	&:hover,
	&:focus,
	&:active {
		border: 1px solid rgba(26, 93, 90, 0.2);
	}
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance: none;
}

textarea {
	width: 100%;
	min-height: 142px;
	resize: none;
}

select {
	border-radius: 10px;
	background: var(--belyy) url('./../img/icons/select-arrow.svg') no-repeat top 8px right 8px / 47px;
	padding: 20px 24px;
	padding-right: 60px;
	font-size: 18px;
	color: var(--tekst-osnovnoy-tekst);
	color: #686d79;
	border: 1px solid transparent;
	outline: none;
	transition: all 0.3s ease;
	position: relative;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;

	& option {
		color: #686d79;
	}

	&:focus {
		background-image: url('./../img/icons/select-arrow-open.svg');
	}
}

.politic {
	display: flex;
	align-items: start;
	gap: 10px;
	line-height: 1.2;
	color: #30423c;
	& a {
		text-decoration: underline;
		color: var(--osnovnye-kdk-k-zelenyy);
		transition: all 0.3s ease;
		&:hover {
			opacity: 0.7;
		}
	}

	& input[type="checkbox"] {
		position: relative;
		width: 24px;
		height: 24px;
		color: #000;
		border: 2px solid var(--osnovnye-kdk-k-zelenyy);
		border-radius: 6px;
		appearance: none;
		cursor: pointer;
		transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
		background: transparent;
		flex-shrink: 0;
		&::before {
			position: absolute;
			content: "";
			display: block;
			top: 50%;
			left: 50%;
			width: 6px;
			height: 10px;
			border-style: solid;
			border-color: var(--belyy);
			border-width: 0 2px 2px 0;
			transform: translate(-50%, -65%) rotate(45deg);
			opacity: 0;
		}
		&:hover {
			background: rgba(26, 93, 90, 0.2);
		}
		&:checked {
			background: var(--osnovnye-kdk-k-zelenyy);
			&::before {
				opacity: 1;
			}
		}
	}
}

.resume {
	display: flex;
	align-items: center;
	gap: 16px;
	margin: 25px 0;

	&:hover {
		& .resume-btn path {
			fill-opacity: 1;
		}
	}

	&-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 50px;
		height: 50px;
		padding: 0;
		position: relative;
		flex-shrink: 0;

		& svg path {
			transition: all 0.3s ease;
		}
	}

	& input[type="file"] {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
	}

	& label {
		display: block;
		font-weight: 500;
		font-size: 18px;
		line-height: 130%;
		color: #30423c;
		margin-bottom: 4px;
	}

	& span {
		font-size: 16px;
		line-height: 130%;
		color: #8f9c96;
	}

	& .file-item {
		display: flex;
		align-items: center;
		gap: 8px;
	}
	& .file-remove {
		cursor: pointer;
	}
}

form.orange {
	& input,
	textarea {
		background: #e15c32;
		color: var(--belyy);
		&::placeholder {
			color: var(--belyy);
		}
		&:hover,
		&:focus,
		&:active {
			border: 1px solid rgba(255, 255, 255, 0.5);
		}
	}
	& .politic {
		color: var(--belyy);
		& a {
			color: var(--belyy);
		}

		& input[type="checkbox"] {
			border: 2px solid var(--belyy);
			&::before {
				border-color: var(--tekst-osnovnoy-tekst);
			}
			&:hover {
				background: rgba(255, 255, 255, 0.2);
			}
			&:checked {
				background: var(--belyy);
			}
		}
	}

	& .resume {
		& label {
			color: var(--belyy);
		}

		& span {
			color: rgba(255, 255, 255, 0.6);
		}
	}
}

@media (max-width: 768px) {
	input,
	textarea {
		padding: 16px 20px;
		font-size: 16px;
		&::placeholder {
			font-size: 16px;
		}
	}
	select {
		padding: 16px 20px;
		padding-right: 50px;
		font-size: 16px;
		background-size: 37px 37px;
	}

	.resume {
		gap: 12px;
		margin: 12px 0;
		&-btn {
			width: 44px;
			height: 44px;
		}
		& label {
			font-size: 16px;
			margin-bottom: 2px;
		}
		& span {
			font-size: 14px;
		}
	}
}


/* accordion */
.accordion {
	-webkit-transition: all 0.4s ease;
	-o-transition: all 0.4s ease;
	transition: all 0.4s ease;
	cursor: pointer;
	width: 100%;
	position: relative;

	&.active {
		.accordion__btn {
			transform: rotate(180deg);
		}
	}

	&__content {
		max-height: 0;
		overflow: hidden;
		-webkit-transition: all 0.4s ease;
		-o-transition: all 0.4s ease;
		transition: all 0.4s ease;
	}

	&__btn {
		transition: all 0.3s ease;
	}
}

.show-btn {
	margin: 40px auto 0;
	min-width: 300px;
}

/* tabs */
.tab-btns {
	display: flex;
	gap: 12px;
}
.tab-btn {
	transition: all 0.3s ease;
	&:hover {
		border: 1px solid rgba(1, 79, 45, 0.4);
	}
	&.active {
		border: none;
		color: #fff;
		background: #78d864;
		display: flex;
		align-items: center;
		gap: 12px;
		&::before {
			content: "";
			display: block;
			width: 8px;
			height: 8px;
			border-radius: 100%;
			flex-shrink: 0;
			background: #fff;
			transition: all 0.3s ease;
		}
	}
}
.tab-content {
	display: none;
	&.tab-content--active {
		display: block;
		width: 100%;
		min-height: 100%;
	}
}

.breadcrumbs {
	margin-bottom: 70px;
}
.breadcrumbs,
.breadcrumbs li {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	gap: 12px;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}
.breadcrumbs li,
.breadcrumbs li a {
	font-size: 12px;
	line-height: 130%;
	text-transform: uppercase;
	color: rgba(177, 220, 205, 0.6);
}
.breadcrumbs li a {
	color: var(--osnovnye-kdk-k-svetlo-zelenyy);
}
.breadcrumbs li a:hover {
	opacity: 0.6;
}
.breadcrumbs li:after {
	content: "";
	display: block;
	width: 5px;
	height: 9px;
	background: url("./../img/icons/arrow-right.svg") no-repeat center / contain;
	flex-shrink: 0;
}
.breadcrumbs li:last-child::after {
	content: none;
}

@media (max-width: 768px) {
	.breadcrumbs {
		margin-bottom: 50px;
	}
	.show-btn {
		margin: 24px auto 0;
		min-width: 100%;
	}
}

.scroll-top {
	position: fixed;
	bottom: 70px;
	left: 40px;
	z-index: 4;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 56px;
	height: 56px;
	background: #007844;
	border: 1px solid #fff;
	border-radius: 50%;
	transition: all 0.3s ease;

	&.hidden {
		opacity: 0;
		pointer-events: none;
		visibility: hidden;
	}
}

@media (max-width: 768px) {
	.scroll-top {
		bottom: 16px;
		left: 16px;
		width: 42px;
		height: 42px;
	}
}

/* rating */
.full-stars {
	text-align: center;
}
.full-stars .rating-group {
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
}
.full-stars input {
	position: absolute;
	left: -9999px;
}
.full-stars label {
	margin: 0;
	cursor: pointer;
}
.full-stars label svg {
	margin: 0 2px;
	height: 18px;
	width: 20px;
	fill: var(--color-temno-zelenyy);
	-webkit-transition: fill 0.3s;
	-o-transition: fill 0.3s;
	transition: fill 0.3s;
}
.full-stars input:checked ~ label svg {
	fill: transparent;
}
.full-stars .rating-group:hover label svg {
	fill: var(--color-temno-zelenyy);
}
.full-stars .rating-group input:hover ~ label svg {
	fill: transparent;
}
