

// awards
.awards {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        height: 100%;
        position: relative;
        border-radius: 20px;
        overflow: hidden;
        padding: 32px;
        display: flex;
        align-items: flex-end;
        min-height: 500px;

        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(transparent, #1A5D5A 69%);
        }
    }

    &__card-pic {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__card-inner {
        position: relative;
        z-index: 3;
    }

    &__card-src {
        display: flex;
        align-items: center;
        gap: 8px;
        font-size: 16px;
        line-height: 1.2;
        color: var(--osnovnye-kdk-k-svetlo-zelenyy);
        margin-bottom: 15px;
    }

    &__card-title {
        font-weight: 500;
        font-size: 28px;
        line-height: 1.1;
        color: var(--belyy);
        margin-bottom: 20px;
    }

    &__card-btn {
    }
}

@media (max-width: 768px) {
    .awards {
        &__card {
            padding: 24px;
            min-height: 400px;
        }
    
        &__card-src {
            font-size: 14px;
            margin-bottom: 14px;
        }
    
        &__card-title {
            font-size: 20px;
        }
    }
}