
// application
.application {

    &__wrap {
        border-radius: 20px;
        padding: 100px;
        background: url('./../img/application/application-bg.svg') no-repeat bottom center , 
            linear-gradient(270deg, #ff6a3a 0%, #ff7144 51.44%, #ff6a3a 99.04%);
        width: 100%;
        min-height: 840px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        color: var(--belyy);
        position: relative;
    }
    
    &__title {
        font-size: 80px;
        max-width: 1300px;
        margin: 0 auto;
        text-align: center;
        position: relative;
        z-index: 2;
    }

    &__bottom {
        display: flex;
        align-items: end;
        justify-content: space-between;
        gap: 40px;
        position: relative;
        z-index: 2;
    }

    &__structure-text {
        font-size: 20px;
        line-height: 1.3;
        color: var(--belyy);
        max-width: 250px;
        margin-bottom: 20px;
    }

    &__structure-photos {
        display: flex;
        //margin-left: -10px;
    }

    &__structure-photo {
        display: block;
        width: 70px;
        height: 70px;
        border-radius: 40px;
        box-shadow: -3px 0 34px 0 rgba(0, 0, 0, 0.15);
        background: var(--belyy);
        overflow: hidden;
        margin-right: -10px;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }
    }

    &__btn {
        min-width: 420px;
    }

    &__pic {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0%);
        width: 55%;
        max-width: 940px;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
            max-height: 585px;
        }
    }
}

@media (max-width: 1400px) {
    .application {

        &__wrap {
            border-radius: 20px;
            padding: 60px 40px;
            min-height: 740px;
        }
        
        &__title {
            font-size: 60px;
            max-width: 1000px;
        }
    
        &__btn {
            min-width: 300px;
        }
    
        &__pic {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-40%, 0%);
            width: 60%;
            max-width: 940px;
            & img {
                max-height: 540px;
            }
        }
    }
    
}
@media (max-width: 1024px) {
    .application {
        &__wrap {
            border-radius: 20px;
            padding: 60px 40px;
            min-height: 740px;
            background: url('./../img/application/application-bg.svg') no-repeat bottom center / 120% 90%, 
            linear-gradient(270deg, #ff6a3a 0%, #ff7144 51.44%, #ff6a3a 99.04%);
        }
        
        &__title {
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            margin-bottom: 16px;
        }
    
        &__bottom {
            flex-direction: column;
            align-items: stretch;
            justify-content: space-between;
            flex-grow: 1;
        }
    
        &__structure-text {
            max-width: none;
            text-align: center;
        }
    
        &__structure-photos {
            display: flex;
            justify-content: center;
        }

    
        &__pic {
            & img {
                max-height: 400px;
            }
        }
    }
    
}
@media (max-width: 768px) {
    .application {
        &__wrap {
            border-radius: 15px;
            padding: 40px 20px;
            min-height: 600px;
        }
        
        &__title {
            font-size: 28px;
            max-width: 1000px;
        }
    
        &__structure-text {
            font-size: 14px;
            margin-bottom: 12px;
        }

        &__structure-photo {
            width: 44px;
            height: 44px;
        }
    
        &__btn {
            min-width: auto;
        }
    
        &__pic {
            transform: translate(-47%, 0%);
            width: 100%;
            max-width: 450px;
            & img {
                max-height: 360px;
            }
        }
    }
    
}
@media (max-width: 500px) {
    .application {
        &__pic {
            & img {
                max-height: 320px;
            }
        }
    }
    
}