// position
.position {
	&__title {
		max-width: 900px;
	}

	&__swiper {
		padding-bottom: 40px;
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
		margin-bottom: 40px;
	}

	&__cards {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 20px;
	}

	&__card {
		position: relative;
		border-radius: 20px;
		padding: 32px;
		height: 100%;
		min-height: 587px;
		background: #649185;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: relative;
		overflow: hidden;

		&::after {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: linear-gradient(180deg, rgba(13, 31, 30, 0.8) 0%, rgba(13, 31, 30, 0) 31.88%, rgba(13, 31, 30, 0) 39.98%, rgba(13, 31, 30, 0.8) 95%);
		}

		&-pic {
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			& img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&-title {
			font-weight: 500;
			font-size: 28px;
			line-height: 110%;
			color: var(--belyy);
			z-index: 2;
		}

		&-bottom {
			z-index: 2;
		}

		&-salary {
			border-radius: 8px;
			padding: 12px 16px;
			background: var(--osnovnye-kdk-osnovnoy-500);
			font-weight: 500;
			font-size: 16px;
			line-height: 130%;
			text-transform: uppercase;
			color: var(--belyy);
			margin-bottom: 16px;
			width: fit-content;
		}

		&-text {
			color: var(--tekst-vtorostepennyy-1);
			padding-top: 16px;
			border-top: 1px solid var(--linii-linii-1-na-temnom);
		}
	}

	&__advantages {
		& ul {
			display: grid;
			grid-template-columns: repeat(4, 1fr);
			gap: 24px;

			& li {
				border-radius: 10px;
				padding: 28px;
				background: rgba(255, 255, 255, 0.15) url("./../img/position/advantage-bg.svg") no-repeat bottom -20px right -50px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				gap: 32px;
				& p {
					font-weight: 500;
					font-size: 32px;
					line-height: 130%;
					color: var(--belyy);
				}
				& span {
					display: block;
					max-width: 80%;
					color: rgba(255, 255, 255, 0.8);
				}
			}
		}
	}
}

@media (max-width: 1400px) {
	.position {
		&__advantages {
			& ul {
				grid-template-columns: repeat(2, 1fr);
				gap: 20px;
			}
		}
	}
}
@media (max-width: 1300px) {
	.position {
		&__cards {
			grid-template-columns: repeat(2, 1fr);
			gap: 20px;
		}
	}
}
@media (max-width: 768px) {
	.position {
		&__swiper {
			padding-bottom: 24px;
			margin-bottom: 24px;
		}

		&__card {
			border-radius: 15px;
			padding: 24px;
			min-height: 500px;

			&-title {
				font-size: 24px;
			}

			&-salary {
				font-size: 12px;
			}
		}

		&__advantages {
			& ul {
				& li {
					padding: 20px;
					background: rgba(255, 255, 255, 0.15) url("./../img/position/advantage-bg.svg") no-repeat bottom 0px right 0px / 30% auto;
					gap: 10px;
					& p {
						font-size: 20px;
					}
				}
			}
		}
	}
}
@media (max-width: 660px) {
	.position {
		&__cards {
			grid-template-columns: repeat(1, 1fr);
			gap: 12px;
		}
	}
}
@media (max-width: 600px) {
	.position {
		&__advantages {
			& ul {
				grid-template-columns: repeat(1, 1fr);
				gap: 8px;
			}
		}
	}
}
