.popup {
	height: 100vh;
	left: 50%;
	opacity: 0;
	pointer-events: none;
	position: fixed;
	top: 50%;
	transform: translate3d(-50%, -50%, 0);
	width: 100%;
	z-index: 2000;
	transition: opacity 0.4s ease;
	overflow-y: auto;
	backdrop-filter: blur(20px);
	background: rgba(0, 0, 0, 0.4);
	display: none;
}

.popup.active {
	display: block;
}

.popup__body {
	width: 100%;
	min-height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 40px 15px 20px;
}

.popup.open {
	opacity: 1;
	pointer-events: all;
}

.popup__content {
	width: 100%;
	max-width: 1160px;
	border-radius: 20px;
	position: relative;
	padding: 60px;
	scale: 0.3;
	transition: scale 0.4s ease;
	background: #e9f1ef;
}

.popup.open .popup__content {
	scale: 1;
}

.close-popup {
	position: absolute;
	top: 30px;
	right: 30px;
	width: 40px;
	height: 40px;
	cursor: pointer;
	border-radius: 10px;
	border: 1px solid #c8d3cf;
	transition: all 0.3s linear;
	padding: 12px;
	display: flex;
	align-items: center;
	justify-content: center;

	&:hover {
		border: 1px solid var(--tekst-osnovnoy-tekst);
	}
}

.popup__title {
	font-weight: 500;
	font-size: 60px;
	line-height: 130%;
	color: #30423c;
	margin-bottom: 40px;
}

.popup__form {
	display: flex;
	flex-direction: column;
	//gap: 36px;

	&-block {
		padding-top: 8px;
		border-top: 1px solid #c8d3cf;
		display: flex;
		flex-wrap: wrap;
		gap: 10px;
		margin-bottom: 36px;
		& > * {
			flex: 1 0 40%;
		}
	}

	&-text {
		flex: 1 0 100%;
		font-size: 14px;
		line-height: 130%;
		color: #8f9c96;
		margin-bottom: 16px;
	}

	& .resume {
		margin: 0;
	}

	&-radio {
		display: flex;
		align-items: center;
		gap: 10px;
		& label {
			font-size: 18px;
			line-height: 130%;
			color: #30423c;
		}

		& input[type="radio"] {
			position: relative;
			width: 22px;
			height: 22px;
			color: #000;
			border: 2px solid #1a5d5a;
			border-radius: 50%;
			appearance: none;
			cursor: pointer;
			transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
			background: transparent;
			flex-shrink: 0;
			&::before {
				position: absolute;
				content: "";
				display: block;
				top: 50%;
				left: 50%;
				width: 8px;
				height: 8px;
				background: #1a5d5a;
				border-radius: 50%;
				transform: translate(-50%, -50%);
				opacity: 0;
			}
			&:checked {
				//background: var(--osnovnye-kdk-k-zelenyy);
				&::before {
					opacity: 1;
				}
			}
		}
	}

	& .politic {
		margin: 40px 0 32px;
	}

	&-btn {
		max-width: 340px;
		background: var(--osnovnye-kdk-k-zelenyy);
	}
}


.popup-response {
	&__content {
		max-width: 700px;
	}

	&__wrapper {
		margin-bottom: 60px;
		& h3 {
			font-weight: 500;
			font-size: 60px;
			line-height: 1.3;
			text-align: center;
			color: #30423c;
			margin-bottom: 16px;
		}
		& p {
			font-size: 20px;
			line-height: 1.3;
			text-align: center;
			color: #30423c;
			opacity: 0.8;
		}
	}

	&__btn {
		width: 100%;
		max-width: 300px;
		margin: 0 auto;
	}
}

@media (max-width: 768px) {
	.popup__content {
		padding: 40px 20px;
	}
	.close-popup {
		top: 10px;
		right: 10px;
		width: 36px;
		height: 36px;
		padding: 13px;
	}
	
	.popup__title {
		font-size: 36px;
		margin-bottom: 32px;
	}

	.popup__form {
		&-block {
			padding-top: 6px;
			gap: 6px;
			margin-bottom: 28px;
			& > * {
				flex: 1 0 40%;
			}
		}
	
		&-text {
			flex: 1 0 100%;
			font-size: 12px;
			margin-bottom: 10px;
		}

		& .politic {
			margin: 10px 0 20px;
		}
	
		&-btn {
			max-width: 100%;
		}
	}

	.popup-response {
		&__wrapper {
			& h3 {
				font-size: 36px;
			}
			& p {
				font-size: 18px;
			}
		}
	
		&__btn {
			max-width: 100%;
		}
	}
}
@media (max-width: 650px) {
	.popup__form {
		&-block {
			flex-direction: column;
			& > * {
				flex: 1 0 100%;
			}
		}
		& .resume {
			margin-bottom: 10px;
		}
	}
}