

// clinics
.clinics {

    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    &__card {
        border-radius: 20px;
        padding: 12px;
        min-height: 640px;
        position: relative;
        overflow: hidden;
        display: flex;
        align-items: end;
    }

    &__card-pic {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__card-inner {
        border-radius: 16px;
        padding: 32px;
        backdrop-filter: blur(25px);
        background: rgba(26, 93, 90, 0.25);
        width: 100%;
    }

    &__card-name {
        font-weight: 500;
        font-size: 36px;
        line-height: 1.1;
        color: var(--belyy);
        margin-bottom: 20px;
    }

    &__card-location {
        color: rgba(255, 255, 255, 0.8);
        display: flex;
        align-items: center;
        gap: 10px;
        &::before {
            content: '';
            display: flex;
            align-items: center;
            justify-content: center;
            width: 32px;
            height: 32px;
            border-radius: 6px;
            background: url('./../img/icons/location.svg') no-repeat center / contain;
            flex-shrink: 0;
        }
    }
}

@media (max-width: 1200px) {
    .clinics {
        &__cards {
            gap: 16px;
        }
    
        &__card {
            padding: 8px;
            min-height: 520px;
        }
    
        &__card-inner {
            padding: 24px;
        }
    
        &__card-name {
            font-size: 28px;
            margin-bottom: 16px;
        }
    }
}
@media (max-width: 768px) {
    .clinics {
        &__card {
            padding: 4px;
            min-height: 400px;
        }
    
        &__card-inner {
            padding: 20px;
        }
    
        &__card-name {
            font-size: 20px;
            margin-bottom: 14px;
        }
    
        &__card-location {
            gap: 8px;
            &::before {
                width: 24px;
                height: 24px;
            }
        }
    }
}

@media (max-width: 660px) {
    .clinics {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 12px;
        }
    }
}