
// team
.team {

    &__row {
        display: flex;
        gap: 20px;
    }

    &__card {
        flex: 1 0 40%;
        max-width: calc(50% - 10px);
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__subtitle {
        opacity: 0.8;
    }

    &__networks {

    }

    &__networks-text {
        font-weight: 500;
        font-size: 14px;
        line-height: 1.3;
        text-transform: uppercase;
        color: #8f9c96;
        margin-bottom: 18px;
    }

    &__swiper {
        overflow: hidden;
        border-radius: 20px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__photo {
        display: block;
        width: 100%;
        height: 100%;
        min-height: 600px;
        max-height: 640px;
    }

    &__photo-pic {
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 20px;
            object-position: top;
        }
    }

    &__swiper-pagination {
        position: absolute;
        bottom: 20px !important;
        left: 20px !important;
        right: 20px;
    }
}


@media (max-width: 1024px) {
    .team {

        &__row {
            flex-direction: column;
            gap: 30px;
        }
    
        &__card {
            flex: 1 0 40%;
            max-width: 100%;
        }
    
        &__content {
            display: contents;
        }
        &__networks {
            order: 3;
        }
        &__photo {
            min-height: 420px;
            max-height: 580px;
        }
    }
}
@media (max-width: 768px) {
    .team {
        &__title {
            margin-bottom: 12px;
        }
    
        &__networks-text {
            font-size: 12px;
            margin-bottom: 16px;
        }
    
        &__swiper {
            border-radius: 15px;
        }
    
        &__photo {
            min-height: 220px;
            max-height: 380px;
        }
    
        &__photo-pic {
            & img {
                border-radius: 15px;
            }
        }
    
        &__swiper-pagination {
            bottom: 12px !important;
            left: 12px !important;
            right: 12px;
        }
    }
}
@media (max-width: 500px) {
    .team {    
        &__photo {
            max-height: 320px;
        }
    }
}