// numbers
.numbers {
	background: #e9f1ef;
	padding: 160px 0;
	position: relative;
	overflow: hidden;

	&::before {
		content: "";
		display: block;
		width: 100%;
		height: 414px;
		background: linear-gradient(180deg, #e9f1ef 56.51%, rgba(238, 239, 247, 0) 100%);
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
	}

	& .section__top {
		position: relative;
		z-index: 3;
	}

	&__title {
		max-width: 800px !important;
		position: relative;
		z-index: 3;
	}

	&__statistic {
		position: absolute;
		top: 0;
		right: 20px;
		z-index: 10;
		& ul {
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			gap: 10px;
			& li {
				border-radius: 15px;
				padding: 28px;
				backdrop-filter: blur(30px);
				background: var(--belyy);
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				gap: 20px;
				flex-grow: 1;
				color: #8f9c96;
				min-height: 240px;
				min-width: 293px;

				& span {
					font-weight: 500;
					font-size: 55px;
					line-height: 100%;
					color: var(--osnovnye-kdk-k-zelenyy);
				}
			}
		}
	}

	&__wrap {
		display: block;
		//position: relative;
		overflow: visible;
		overflow-y: visible;

		& .container {
			position: relative;
		}
	}

	&__map {
		display: block;
		position: relative;
		overflow-y: visible;
		min-height: 805px;
		width: 2200px;
		//overflow-x: auto;
		&-pic {
			position: absolute;
			bottom: 0;
			left: 20px;
			width: 110%;
			& img {
				width: 2200px;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__city {
		position: absolute;
		display: flex;
		align-items: flex-end;
		gap: 37px;

		&:nth-child(1) {
			left: 16%;
			bottom: 32%;
		}
		&:nth-child(2) {
			left: 16%;
			bottom: 40%;
		}
		&:nth-child(3) {
			left: 12%;
			bottom: 25%;
		}
		&:nth-child(4) {
			left: 17%;
			bottom: 76%;
		}
	}

	&__dot {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: #1a5d5a;
		outline: 30px solid rgba(26, 93, 90, 0.1);
		position: relative;
		z-index: 5;
		font-size: 16px;
		line-height: 120%;
		color: var(--belyy);
		flex-shrink: 0;
		transition: all 0.3s ease;
		& span {
			opacity: 0;
			pointer-events: none;
			transition: all 0.3s ease;
		}

		&.tab-btn--active {
			width: 134px;
			height: 134px;
			background: var(--osnovnye-kdk-osnovnoy-500);
			transform: translate(-40px, 40px);
			outline: none;
			z-index: 7;
			& span {
				opacity: 1;
			}
			&::after {
				content: "";
				display: block;
				width: 170px;
				height: 170px;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				border-radius: 100%;
				border: 1px solid rgba(240, 74, 37, 0.5);
			}
		}
	}

	&__info {
		border-radius: 15px;
		background: var(--osnovnye-kdk-k-zelenyy);
		padding: 24px;
		min-width: 293px;

		position: absolute;
		bottom: -40px;
		left: calc(100% - 40px);
		z-index: 6;
		& ul {
			display: flex;
			flex-direction: column;
			gap: 20px;
			& p {
				font-size: 16px;
				line-height: 120%;
				color: var(--osnovnye-kdk-k-svetlo-zelenyy);
				margin-bottom: 10px;
			}
			& span {
				font-weight: 500;
				font-size: 24px;
				line-height: 120%;
				text-align: right;
				color: var(--belyy);
				display: flex;
				align-items: center;
				gap: 10px;
				&::before {
					content: "";
					display: block;
					width: 6px;
					height: 6px;
					border-radius: 100%;
					background: var(--osnovnye-kdk-osnovnoy-500);
				}
			}
		}
	}
}

