@media (max-width: 768px) {
	.numbers {
		padding: 80px 0;
		&::before {
			height: 290px;
		}

		&__statistic {
			& ul {
				flex-direction: column;
				gap: 16px;
				& li {
					padding: 24px;
					gap: 36px;
					min-height: 155px;
					& span {
