// vacancies
.vacancies {
	&__title {
		max-width: 700px;
	}

	&__cards {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 20px;
	}

	&__card {
		border-radius: 20px;
		padding: 40px;
		background: #e9f1ef;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		gap: 80px;

		&-top {
			display: flex;
			align-items: center;
			justify-content: space-between;
			gap: 20px;
			margin-bottom: 32px;
		}

		&-title {
			font-weight: 500;
			font-size: 36px;
			line-height: 120%;
			color: #30423c;
		}

		&-city {
			color: #30423c;
			display: flex;
			align-items: center;
			gap: 12px;

			&::before {
				content: "";
				display: block;
				width: 32px;
				height: 32px;
				background: url("./../img/icons/location.svg") no-repeat center / contain;
				flex-shrink: 0;
			}
		}

		&-text {
			color: #30423c;
			opacity: 0.8;
			padding-bottom: 32px;
			border-bottom: 1px solid #c8d3cf;
			margin-bottom: 32px;
		}

		&-tags {
			& ul {
				display: flex;
				flex-wrap: wrap;
				gap: 10px;
				& li {
					border-radius: 10px;
					padding: 12px 16px;
					background: rgba(177, 220, 205, 0.3);
					font-weight: 500;
					font-size: 16px;
					line-height: 130%;
					color: var(--osnovnye-kdk-k-zelenyy);
					&:first-child {
						background: var(--osnovnye-kdk-k-svetlo-zelenyy);
					}
				}
			}
		}

		&-btns {
			display: flex;
			gap: 10px;
			& > * {
				flex: 1 0 40%;
			}
		}
	}
}

@media (max-width: 1300px) {
	.vacancies {
		&__card {
			padding: 30px;

			&-title {
				font-size: 32px;
			}

			&-btns {
				& > * {
					flex-basis: auto;
				}
			}
		}
	}
}
@media (max-width: 1024px) {
	.vacancies {
		&__cards {
			grid-template-columns: repeat(1, 1fr);
			gap: 20px;
		}
	}
}
@media (max-width: 768px) {
	.vacancies {
		&__cards {
			gap: 12px;
		}

		&__card {
			border-radius: 15px;
			padding: 20px;
			gap: 30px;

			&-top {
				flex-direction: column;
				align-items: stretch;
				gap: 16px;
				margin-bottom: 16px;
			}

			&-title {
				font-size: 20px;
			}

			&-city {
				font-size: 14px;
				gap: 10px;
				&::before {
					width: 24px;
					height: 24px;
				}
			}

			&-text {
				padding-bottom: 20px;
				margin-bottom: 20px;
			}

			&-tags {
				& ul {
					gap: 8px;
					& li {
						padding: 12px 14px;
						font-size: 14px;
					}
				}
			}

			&-btns {
				flex-direction: column;
			}
		}
	}
}
