// form
.form {
	&__wrap {
		display: flex;
		border-radius: 20px;
		background: var(--osnovnye-kdk-k-oranzhevyy);
	}

	&__content {
		padding: 60px;
		flex: 1 0 50%;
	}

	&__title {
		padding-bottom: 32px;
		border-bottom: 1px solid rgba(255, 255, 255, 0.1);
		margin-bottom: 32px;
	}

	&__inner {
		display: flex;
		flex-wrap: wrap;
		gap: 12px;

		& .politic {
			flex: 1 0 100%;
			margin-bottom: 12px;
		}
	}

	&__input {
		flex: 1 0 35%;
		max-width: calc(50% - 6px);
	}

	&__btn {
		min-width: 420px;
	}

	&__pic {
		flex: 1 0 50%;
		display: block;
		border-radius: 20px;
		overflow: hidden;
		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

@media (max-width: 1460px) {
	.form {
		&__content {
			padding: 40px;
		}
		&__title {
			font-size: 46px;
		}
	}
}
@media (max-width: 1100px) {
	.form {
		&__pic {
			display: none;
		}
	}
}
@media (max-width: 768px) {
	.form {
		&__wrap {
			border-radius: 10px;
		}

		&__content {
			padding: 24px;
		}

		&__title {
			font-size: 28px;
			padding-bottom: 20px;
			margin-bottom: 20px;
		}

		&__inner {
			gap: 6px;
		}

		&__input {
			flex: 1 0 35%;
			max-width: calc(50% - 6px);
		}

		&__btn {
			min-width: auto;
			width: 100%;
		}
	}
}
@media (max-width: 600px) {
	.form {
		&__inner {
			flex-direction: column;
		}

		&__input {
			max-width: 100%;
		}
	}
}
