
// header
.header {
    box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.06);
    background: var(--belyy);

    background: linear-gradient(270deg, #1a5d5a 0%, #33827f 28.66%, #1a5d5a 100%);
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 20;
    transition: all 0.3s ease;

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 40px;
        padding: 28px 0;
    }

    &__logo-white {
        display: none;
    }

    &__menu {
        display: contents;
    }

    &__nav {
        transform: translate(30%, 0%);
        
    }

    &__nav-list {
        display: flex;
        gap: 4px;
        border-radius: 10px;
        background: rgba(255, 255, 255, 0.1);
        overflow: hidden;

        & a {
            display: block;
            padding: 20px 24px;
            font-weight: 500;
            font-size: 20px;
            line-height: 1.2;
            color: var(--belyy);
            transition: all 0.3s ease;
            &:hover {
                background: var(--osnovnye-kdk-k-zelenyy);
            }
        }
    }

    &__contacts {
        display: flex;
        align-items: center;
        gap: 24px;

        & span {
            display: none;
        }

        &-link {
            font-weight: 500;
            font-size: 20px;
            line-height: 1.2;
            text-align: right;
            color: var(--belyy);
            display: flex;
            align-items: center;
            gap: 24px;

            
            &:hover {
                text-decoration: underline;
            }
        }
    }
    
    &__contact:first-child {
        & .header__contacts-link::after {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            border-radius: 100%;
            background: #F04A25;
        }
    }


    &__btns {
        display: flex;
        gap: 5px;

        &-link {
            padding: 10px 12px;
            border: 1px solid rgba(255, 255, 255, 0.1);
            border-radius: 10px;
            font-weight: 500;
            font-size: 14px;
            line-height: 1.3;
            text-transform: uppercase;
            color: var(--belyy);
        }
    }
}

.header-white {
    box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.06);
    background: var(--belyy);

    & .header {
        &__logo {
            display: none;
        }
        &__logo-white {
            display: block;
        }

        &__nav-list {
            & a {
                color: #30423c;
                &:hover {
                    background: transparent;
                    color: #1a5d5a;
                }
            }
        }
    
        &__contacts {
            &-link {
                color: #30423c;
            }
        }
    
        &__btns {
            &-link {
                border: 1px solid #c8d3cf;
                color: #30423c;
            }
        }
    }
    
    & .burger {
        border: 1px solid #c8d3cf;
        & span {
            background-color: #30423c;
        }
    }
}

main {
    margin-top: 120px;
}

.header-mobile {
    display: none;
}
.burger {
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    width: 40px;
    height: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 4px;
    flex-shrink: 0;
    cursor: pointer;
    transition: all 0.3s ease;

    & span {
        display: block;
        width: 18px;
        height: 2px;
        background-color: var(--belyy);
        border-radius: 5px;
        transition: all 0.3s ease;
    }

    &.active {
        & span:first-child {
            transform: rotate(45deg) translate(4px, 3px);
        }
        & span:nth-child(2) {
            opacity: 0;
        }
        & span:last-child {
            transform: rotate(-45deg) translate(5px, -4px);
        }
    }
}

@media (hover :hover) {
    .header__nav-item:hover {
        background: #e1f0e1;
        & .header__nav-arrow  {
            transform: rotate(180deg);
        }
        & > .header__nav-sublist {
            opacity: 1;
            visibility: visible;
            pointer-events: all;
        }
        &.header__nav-itemService {
            background: #78d864;
        }
    }
    .header__nav-sublist li:hover {
        & > .header__nav-subsublist {
            opacity: 1;
            visibility: visible;
            pointer-events: all;
        }
    }
}


@media (max-width: 1500px) {
    .header {
        &__nav {
            transform: translate(18%, 0%);
            
        }
    }
}
@media (max-width: 1300px) {
    .header {
        &__row {
            padding: 24px 0;
        }
        &__nav {
            transform: none;
            
        }
        &__contacts {
            flex-direction: column;
            align-items: stretch;
            gap: 12px;
            &-link {
                font-size: 18px;
                gap: 8px;
                &::after {
                    content: '';
                    display: block;
                    width: 6px;
                    height: 6px;
                    border-radius: 100%;
                    background: #F04A25;
                    order: -1;
                }
            }
        }
    }
    main {
        margin-top: 112px;
    }
}
@media (max-width: 1024px) {
    .header-mobile {
        display: block;
    }
    .main {
        margin-top: 70px;
    }
    .header {
        &__row {
            padding: 20px 0;
        }

        &__logo {
            max-width: 148px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &__menu {
            position: absolute;
            top: 101%;
            left: -100%;
            width: 100%;
            padding: 30px 20px 20px;
            display: flex;
            align-items: stretch;
            justify-content: flex-start;
            flex-direction: column;
            gap: 24px;
            background: linear-gradient(270deg, #1a5d5a 0%, #33827f 28.66%, #1a5d5a 100%);
            z-index: 10;
            opacity: 0;
            pointer-events: none;
            transition: all 0.3s ease;
            height: calc(100vh - 80px);
            overflow-y: auto;
            &.active {
                opacity: 1;
                pointer-events: all;
                left: 0;
            }
            &::-webkit-scrollbar {
                width: 3px;
                background: transparent;
            }
            &::-webkit-scrollbar-thumb {
                width: 3px;
                border-radius: 20px;
                background: #1a5d5a;
            }
        }

        &__nav {
            flex-grow: 1;
        }

        &__nav-list {
            flex-direction: column;
            gap: 16px;
            border-radius: 0;
            background: transparent;
            overflow: hidden;
    
            & a {
                padding: 0;
                &:hover {
                    background: transparent;
                    text-decoration: underline;
                }
            }

        }
        
        &__contacts {
            gap: 16px;
            padding-bottom: 24px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

            &-link {
                font-size: 18px;
                text-align: left;
            }
            & span {
                display: block;
                font-size: 14px;
                line-height: 1.2;
                color: var(--osnovnye-kdk-k-svetlo-zelenyy);
                margin-bottom: 6px;
            }
        }
    }
}
