
// faq
.faq {

    &__list {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__item {
        border-radius: 15px;
        padding-block: 28px;
        padding-inline: 36px 28px;
        background: #e9f1ef;

        &.active {
            & .faq__item-btn {
                border: 1px solid var(--osnovnye-kdk-k-oranzhevyy);
            }
        }
    }

    &__item-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 16px;
    }

    &__item-question {
        font-weight: 500;
        font-size: 28px;
        line-height: 1.2;
        color: #30423c;
        flex-grow: 1;
    }

    &__item-btn {
        border-radius: 10px;
        border: 1px solid #c8d3cf;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        flex-shrink: 0;
    }

    &__item-answer {
        margin-top: 16px;
        padding-right: 76px;
    }
}

@media (max-width: 1024px) {
    .faq {
        &__item-question {
            font-size: 24px;
        }
    }
}
@media (max-width: 768px) {
    .faq {
        &__list {
            gap: 8px;
        }
    
        &__item {
            border-radius: 15px;
            padding: 20px;
        }

        &__item-question {
            font-size: 18px;
        }
    
        &__item-btn {
            width: 40px;
            height: 40px;
            & svg {
                max-width: 14px;
            }
        }
    
        &__item-answer {
            padding-right: 0;
        }
    }
}