/* swiper CUSTOM */

.swiper {
    width: 100%;
    height: 100%;
}

/* swiper-buttons */
.swiper-btns {
    display: flex;
    align-items: center;
    gap: 8px;
}
.swiper-button-next, .swiper-button-prev {
    position: static;
    margin: 0;
    width: 69px;
    height: 50px;
    border-radius: 10px;
    border: 1px solid #c8d3cf;
    flex-shrink: 0;
    transition: all 0.3s ease;
}
.swiper-button-next:after, .swiper-button-prev:after {
    color:  #676c6a;
    font-size: 14px;
    font-weight: 700;
    transition: all 0.3s ease;
}
.swiper-button-next:hover,.swiper-button-prev:hover {
    border: 1px solid var(--osnovnye-kdk-k-zelenyy);
}
.swiper-button-next:hover:after, .swiper-button-prev:hover:after {
    color: var(--osnovnye-kdk-k-zelenyy);
}

@media (max-width: 768px) {
    .swiper-btns {
        display: none;
    }
}


/* swiper-pagination  */
.swiper-pagination {
    position: static;
    width: auto !important;
    display: flex;

    &-bullet {
        flex-grow: 1;
        position: static;
        border-radius: 32px;
        width: 25px;
        height: 6px;
        background: var(--osnovnye-kdk-k-zelenyy);
    }
}

.swiper-pagination-fraction {
    font-weight: 700;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);

    .swiper-pagination-current {
        font-weight: 700;
        font-size: 26px;
        color: var(--belyy);
    }
}

.swiper-scrollbar {
    position: static !important;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    &-drag {
        background: var(--belyy);
        border-radius: 10px;
    }
}
