
// stories
.stories {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        height: auto;
        opacity: 0.1;
        transition: all 0.3s ease;

        &.swiper-slide-active {
            opacity: 1;
        }
    }

    &__card {
        border-radius: 20px;
        padding: 80px;
        background: url('./../img/intro/intro-bg.svg') no-repeat top right -60% / 70% 100%, 
            linear-gradient(270deg, #1a5d5a 0%, #3a918d 21.66%, #1a5d5a 100%);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 50px;
        position: relative;
        height: 100%;
        min-height: 640px;

        &::after {
            content: '';
            display: block;
            width: 282px;
            height: 242px;
            background: url('./../img/icons/quote.svg') no-repeat center / contain;
            position: absolute;
            top: -64px;
            left: 24px;
        }
    }

    &__card-main {
        max-width: 60%;
    }
    
    &__card-text {
        font-size: 20px;
        line-height: 1.4;
        color: var(--belyy);
    }

    &__card-btn {
        border-radius: 5px;
        padding-block: 8px;
        padding-inline: 12px;
        background: rgba(255, 255, 255, 0.1);
        font-size: 14px;
        line-height: 1.2;
        color: var(--belyy);
        margin-top: 12px;
        display: flex;
        align-items: center;
        gap: 10px;
        &::after {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            border-bottom: 1.5px solid var(--belyy);
            border-right: 1.5px solid var(--belyy);
            transform: rotate(45deg);
            margin-bottom: 4px;
            transition: all 0.3s ease;
        }

        &.active {
            &::after {
                transform: rotate(225deg);
                margin-top: 5px;
            }
        }
    }

    &__card-author {
        border-left: 2px solid var(--belyy);
        padding-block: 8px;
        padding-inline: 16px 0px;
        max-width: 60%;
    }

    &__card-name {
        display: flex;
        align-items: center;
        gap: 12px;
        font-weight: 500;
        font-size: 32px;
        line-height: 1.1;
        color: var(--belyy);
        margin-bottom: 16px;
    }

    &__card-photo {
        position: absolute;
        bottom: 0;
        right: 25px;
        max-width: 40%; 
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__card-posts {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 12px 40px;
        font-size: 20px;
        line-height: 1.1;
        color: var(--belyy);
    }

    &__card-post {
        display: flex;
        align-items: center;
        gap: 8px;
        &::after {
            content: '';
            display: block;
            width: 14px;
            height: 14px;
            border-radius: 100%;
            background: var(--belyy);
            border: 3px solid  #206763;
            outline: 2px solid  rgba(255, 255, 255, 0.1);
        }
        
        &:last-child {
            flex-direction: row-reverse;
        }
    }

    &__card-year {
        position: relative;
        
        & span {
            display: block;
            font-size: 16px;
            border: 2px solid #226a67;
            border-radius: 30px;
            padding: 8px 10px;
            background: #387976;
            position: relative;
            z-index: 2;
        }

        &::after {
            content: '';
            display: block;
            width: calc(100% + 65px);
            height: 28px;
            background: url('./../img/icons/arrow.svg') no-repeat center right / cover;
            position: absolute;
            left: -20px;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            z-index: 1;
        }
    }
}

@media (max-width: 1300px) {
    .stories {
        &__card {
            padding: 40px;
        }
    }
}

@media (max-width: 1024px) {
    .stories {
        &__swiper-slide {
            opacity: 1;
        }
        
        &__card {
            background: url('./../img/intro/intro-bg.svg') no-repeat bottom -180% right -240% / 80% 80%, 
                linear-gradient(270deg, #1a5d5a 0%, #317b77 32.18%, #1a5d5a 83.23%);
            min-height: 600px;
        }
    
        &__card-main {
            max-width: 100%;
        }
    
        &__card-author {
            max-width: 100%;
        }
    
        &__card-photo {
            position: static;
            display: block;
            width: 48px;
            height: 48px;
            background: #316e6b;
            border: 5px solid #256562;
            border-radius: 60px;
            overflow: hidden;
            & img {
                width: 48px;
                height: 48px;
                object-position: top;
            }
        }
    }
    
}
@media (max-width: 768px) {
    .stories {
        &__card {
            border-radius: 15px;
            padding-block: 40px;
            padding-inline: 20px;
            gap: 28px;
            min-height: auto;
    
            &::after {
                width: 160px;
                height: 137px;
                top: -60px;
                left: 14px;
            }
        }

        &__card-text {
            font-size: 16px;
            max-height: 220px;
            overflow: hidden;
            transition: all 0.3s ease;
        }
    
        &__card-author {
            border-left: 1px solid var(--belyy);
            padding-block: 4px;
            padding-inline: 16px 0px;
        }
    
        &__card-name {
            font-size: 18px;
        }
    
        &__card-posts {
            flex-direction: column;
            align-items: start;
            gap: 12px;
            font-size: 16px;
        }
    
        &__card-post {
            flex-direction: row-reverse;
            gap: 6px;
            &::after {
                width: 12px;
                height: 12px;
                border: 2px solid  #206763;
                outline: 2px solid  rgba(255, 255, 255, 0.1);
            }
        }
    
        &__card-year {
            position: relative;
            
            & span {
                display: flex;
                align-items: center;
                gap: 6px;
                font-size: 12px;
                padding: 6px 10px 6px 4px;

                &::before {
                    content: '';
                    display: block;
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background: var(--osnovnye-kdk-k-zelenyy);
                }
            }
    
            &::after {
                content: '';
                display: block;
                height: 20px;
                width: 45px;
                left: 0%;
                top: 50%;
                transform: translate(-36%, -50%) rotate(90deg);
                z-index: 1;
            }
        }
    }
    
}