
// media
.media {
    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 40px 20px;
    }

    &__card {
        display: block;
        &:hover {
            & .media__card-title {
                color: var(--osnovnye-kdk-k-oranzhevyy);
            }

            & .media__card-video {
                &::after {
                    background: rgba(0, 0, 0, 0.6);
                }
                &::before {
                    border: 1px solid transparent;
                    background: rgba(255, 255, 255, 0.2) url('./../img/icons/play.svg') no-repeat center;
                }
            }
        }
    }

    &__card-pic {
        display: block;
        width: 100%;
        margin-bottom: 20px;
        & img {
            width: 100%;
            height: 220px;
            object-fit: cover;
            border-radius: 10px;
        }
    }

    &__card-video {
        display: block;
        width: 100%;
        height: 220px;
        border-radius: 10px;
        margin-bottom: 20px;
        position: relative;
        overflow: hidden;
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.5);
            position: absolute;
            top: 0;
            left: 0;
            transition: all 0.3s ease;
        }

        &::before {
            content: '';
            display: block;
            width: 60px;
            height: 60px;
            border-radius: 40px;
            border: 1px solid var(--belyy);
            background: url('./../img/icons/play.svg') no-repeat center;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 2;
            transition: all 0.3s ease;
        }

        & img {
            width: 100%;
            height: 220px;
            object-fit: cover;
            border-radius: 10px;
        }
    }
    
    &__card-info {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 16px;
        line-height: 1.4;
        margin-bottom: 16px;
    }

    &__card-tag {
        color: var(--osnovnye-kdk-osnovnoy-500);
        display: flex;
        align-items: center;
        gap: 10px;

        &::after {
            content: '';
            display: block;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background: var(--osnovnye-kdk-osnovnoy-500);
        }
    }

    &__card-date {
        color: #686d79;
    }

    &__card-title {
        font-weight: 500;
        font-size: 28px;
        line-height: 1.1;
        color: var(--tekst-osnovnoy-tekst);
        margin-bottom: 16px;
        transition: all 0.3s ease;
    }

    &__card-desciption {
        font-size: 16px;
        line-height: 1.4;
        color: #686d79;
        max-height: 66px;
        overflow: hidden;
    }

    &__btn {
        margin: 60px auto 0;
    }
}

@media (max-width: 1300px) {
    .media {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}
@media (max-width: 1024px) {
    .media {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}
@media (max-width: 768px) {
    .media {
        &__cards {
            gap: 20px 12px;
        }

        &__card {
            &:hover {
                & .media__card-video {
                    &::before {
                        background: rgba(255, 255, 255, 0.2) url('./../img/icons/play.svg') no-repeat center / 9px 11px;
                    }
                }
            }
        }

        &__card-pic {
            margin-bottom: 12px;
            & img {
                height: 160px;
            }
        }

        &__card-video {
            height: 160px;
            margin-bottom: 12px;
            &::before {
                width: 40px;
                height: 40px;
                background: url('./../img/icons/play.svg') no-repeat center / 9px 11px;
            }
            & img {
                height: 160px;
            }
        }
        
        &__card-info {
            gap: 8px;
            font-size: 14px;
            margin-bottom: 8px;
        }
    
        &__card-tag {
            gap: 8px;
        }
    
        &__card-title {
            font-size: 20px;
            margin-bottom: 10px;
        }
    
        &__card-desciption {
            font-size: 14px;
            max-height: 54px;
        }
    }
}
@media (max-width: 600px) {
    .media {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}