
// reviews
.reviews {
    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__card {
        border-radius: 20px;
        padding: 32px;
        background: #e9f1ef;
    }

    &__card-top, &__card-recomend {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 16px;
        padding-bottom: 20px;
        border-bottom: 1px dashed var(--osnovnye-kdk-k-svetlo-zelenyy);
        margin-bottom: 20px;
    }

    &__card-rating {
        display: flex;
        align-items: center;
        gap: 8px;
        border-radius: 16px;
        padding: 20px 18px;
        background: var(--osnovnye-kdk-k-zelenyy);
        font-weight: 500;
        font-size: 30px;
        line-height: 1;
        color: var(--belyy);
        flex-shrink: 0;
    }
    
    &__card-logo {
        display: block;
        max-width: 80%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    
    &__card-recomend {
        font-weight: 500;
        font-size: 20px;
        line-height: 1.3;
        text-transform: uppercase;
        color: rgba(19, 60, 58, 0.5);
        & span {
            color: var(--osnovnye-kdk-k-zelenyy);
            text-align: right;
        }
    }
}

@media (max-width: 1540px) {
    .reviews {
        &__card {
            padding: 26px;
        }
    
        &__card-rating {
            font-size: 24px;
        }
        
        &__card-recomend {
            font-size: 18px;
        }
    }
}
@media (max-width: 1400px) {
    .reviews {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 768px) {
    .reviews {
        &__cards {
            gap: 10px;
        }
    
        &__card {
            border-radius: 15px;
            padding: 20px;
        }
    
        &__card-top, &__card-recomend {
            padding-bottom: 14px;
            margin-bottom: 14px;
        }
    
        &__card-rating {
            gap: 6px;
            border-radius: 8px;
            padding: 10px 15px;
            font-size: 20px;
        }
        
        &__card-logo {
            & img {
                max-height: 24px;
            }
        }
        
        &__card-recomend {
            font-size: 14px;
        }
    }
}
@media (max-width: 600px) {
    .reviews {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
    }
}