@charset "UTF-8";
/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --container-width: 1840px;
  --container-padding: 20px;
  --font-family: "InterTight", "Inter";
  --second-family: "Inter", sans-serif;
  --belyy: #fff;
  --plashka: #eeeff7;
  --knopki-«arrow»: #68676c;
  --knopki-«arrow»-2: #eaebec;
  --temnyy-blok-500: #004058;
  --temnyy-blok-400: #21424f;
  --linii-linii-1-na-temnom: #70767c;
  --linii-linii-2-na-svetlom: #eaebec;
  --linii-liniy-3-v-temnom-bloke: #265d71;
  --linii-liniya-4: #c8c9d3;
  --osnovnye-kdk-osnovnoy-550: #e25333;
  --osnovnye-kdk-osnovnoy-500: #f04a25;
  --osnovnye-kdk-osnovnoy-550: #006389;
  --osnovnye-kdk-osnovnoy-100: #65d2fc;
  --osnovnye-kdk-osnovnoy-50: #ebf5f9;
  --tekst-osnovnoy-tekst: #303242;
  --tekst-vtorostepennyy-1: #b8b7b8;
  --tekst-vtorostepennyy-2: #8f909c;
  --tekst-vtorostepennyy-3-temnyy-blok: #7c939b;
  --tekst-vtorostepennyy-4-v-temnom-bloke: #84c4dc;
  --tekst-vtorostepennyy-tekst-5-na-oranzhevom: #f7a492;
  --osnovnye-kdk-k-oranzhevyy: #ff6a3a;
  --osnovnye-kdk-k-svetlo-oranzhevyy: #ffb49c;
  --osnovnye-kdk-k-zelenyy: #1a5d5a;
  --osnovnye-kdk-k-temnyy-zelenyy: #133c3a;
  --osnovnye-kdk-k-zelenovatyy: #319995;
  --osnovnye-kdk-k-svetlo-zelenyy: #b1dccd;
  --osnovnye-kdk-k-seryy: #e8f3ef;
}

/* InterTight */
@font-face {
  font-family: "InterTight";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/InterTight-Regular.woff2") format("woff2"), url("../fonts/InterTight-Regular.woff") format("woff");
}
/* Inter */
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  font-weight: 600;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}
/* Общие стили */
html.lock {
  overflow: hidden;
}

html.burger-lock {
  overflow: hidden;
}

html::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: fixed;
  inset: 0;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
  -webkit-transition: opacity 0.3s linear;
  -o-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}

.no-scroll {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.burger-lock {
  overflow: hidden;
}

body {
  margin: 0;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0;
  background-color: #fff;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 1.3;
  color: #686d79;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-padding);
  padding: 0 30px;
  max-width: var(--container-width);
  width: 100%;
}

section {
  overflow: hidden;
}

.section-offset {
  margin-bottom: 160px;
}

.section__top {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 32px;
  padding-top: 20px;
  border-top: 1px solid #c8d3cf;
  font-size: 16px;
  line-height: 1.3;
  text-transform: uppercase;
  color: #8f9c96;
}

.green-block {
  padding: 160px 0;
  background: var(--osnovnye-kdk-k-zelenyy);
  color: var(--belyy);
}
.green-block .section__top {
  border-color: rgba(255, 255, 255, 0.2);
  color: #aabeb5;
}
.green-block .title-h2 {
  color: var(--belyy);
}

@media (max-width: 1200px) {
  .section-offset {
    margin-bottom: 120px;
  }
  .section__top {
    margin-bottom: 40px;
  }
  .section-padding {
    padding: 60px 0;
  }
  .swiper {
    overflow: visible;
  }
  .green-block {
    padding: 120px 0;
  }
}
@media (max-width: 768px) {
  .section-offset {
    margin-bottom: 80px;
  }
  .container {
    padding: 0 var(--container-padding);
  }
  .section__top {
    padding-top: 8px;
    font-size: 12px;
    margin-bottom: 20px;
  }
  .section__top img {
    max-height: 8px;
    object-fit: contain;
  }
  body {
    font-size: 16px;
  }
  .green-block {
    padding: 80px 0;
  }
}
.title-h1,
.title-h2 {
  font-family: var(--font-family);
  font-weight: 500;
  line-height: 1.1;
  color: #30423c;
}

.title-h1 {
  font-size: 92px;
  color: var(--belyy);
}

.title-h2 {
  font-size: 60px;
}

.section__title {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 40px;
  margin-bottom: 60px;
}
.section__title .title-h2 {
  max-width: 1005px;
}

@media (max-width: 1300px) {
  .title-h1 {
    font-size: 64px;
  }
  .title-h2 {
    font-size: 50px;
  }
  .section__title {
    margin-bottom: 50px;
  }
}
@media (max-width: 768px) {
  .title-h1 {
    font-size: 38px;
  }
  .title-h2 {
    font-size: 30px;
  }
  .section__title {
    margin-bottom: 30px;
  }
}
.logo img {
  max-width: 186px;
  max-height: 40px;
  min-width: 148px;
  min-height: 32px;
  object-fit: contain;
}

@media (max-width: 1400px) {
  .logo {
    gap: 9px;
  }
  .logo__picture img {
    width: 46px;
    height: 46px;
  }
  .logo__text p {
    font-size: 28px;
    margin-bottom: 2px;
  }
  .logo__text span {
    font-size: 14px;
  }
}
@media (max-width: 768px) {
  .logo__text p {
    font-size: 22px;
  }
  .logo__text span {
    font-size: 10px;
  }
}
.primary-btn,
.secondary-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  border-radius: 16px;
  padding: 32px;
  background: var(--belyy);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  line-height: 1.3;
  text-transform: uppercase;
  color: #30423c;
  transition: all 0.2s ease-in;
}
.primary-btn::after,
.secondary-btn::after {
  content: "";
  display: inline-block;
  width: 24px;
  height: 24px;
  background-color: #f04a25;
  -webkit-mask-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.515 8.24208L5.91752 19.8396L4.16039 18.0825L15.7579 6.48496H4L5.38053 4L20 4V18.6195L17.515 20V8.24208Z' fill='black'/%3E%3C/svg%3E");
  mask-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.515 8.24208L5.91752 19.8396L4.16039 18.0825L15.7579 6.48496H4L5.38053 4L20 4V18.6195L17.515 20V8.24208Z' fill='black'/%3E%3C/svg%3E");
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-size: contain;
  mask-size: contain;
  transition: all 0.3s ease;
}
.primary-btn.small-btn,
.secondary-btn.small-btn {
  border-radius: 10px;
  padding: 24px;
  font-size: 16px;
  background: var(--osnovnye-kdk-k-zelenovatyy);
  color: var(--belyy);
}
.primary-btn.small-btn::after,
.secondary-btn.small-btn::after {
  background-color: var(--belyy);
}

.primary-btn:hover {
  background: rgba(255, 255, 255, 0.9);
}
.primary-btn:hover::after {
  transform: rotate(45deg);
}

.secondary-btn:hover {
  background: var(--osnovnye-kdk-k-zelenyy);
  color: var(--belyy);
}
.secondary-btn:hover::after {
  background: var(--belyy);
  transform: rotate(45deg);
}

.small-btn:hover {
  background: var(--belyy);
  color: var(--tekst-osnovnoy-tekst);
}
.small-btn:hover::after {
  background-color: var(--osnovnye-kdk-k-oranzhevyy);
}

.btn-simple {
  display: block;
  border-radius: 10px;
  padding-block: 15px;
  padding-inline: 18px;
  background: var(--osnovnye-kdk-k-zelenyy);
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  text-transform: uppercase;
  color: var(--belyy);
  text-align: center;
  transition: all 0.3s ease;
}
.btn-simple:hover {
  background: rgba(26, 93, 90, 0.9);
}
.btn-simple.white {
  border: 1px solid rgba(26, 93, 90, 0.5);
  background: transparent;
  color: var(--osnovnye-kdk-k-zelenyy);
}
.btn-simple.white:hover {
  color: var(--belyy);
  background: var(--osnovnye-kdk-k-zelenyy);
}

.btn-show {
  display: block;
  border: 1px solid rgba(26, 93, 90, 0.5);
  border-radius: 10px;
  padding: 15px 24px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  text-transform: uppercase;
  color: var(--osnovnye-kdk-k-zelenyy);
  width: fit-content;
  transition: all 0.3s ease;
}
.btn-show:hover {
  border: 1px solid var(--osnovnye-kdk-k-zelenyy);
  color: var(--osnovnye-kdk-k-zelenyy);
}

@media (max-width: 768px) {
  .primary-btn,
  .secondary-btn {
    border-radius: 12px;
    padding: 20px;
    font-size: 14px;
  }
  .primary-btn::after,
  .secondary-btn::after {
    width: 18px;
    height: 18px;
  }
  .primary-btn.small-btn,
  .secondary-btn.small-btn {
    padding: 19px 18px;
    font-size: 14px;
  }
  .primary-btn.small-btn::after,
  .secondary-btn.small-btn::after {
    width: 12px;
    height: 12px;
  }
  .btn-simple {
    padding-block: 14px;
    padding-inline: 16px;
    font-size: 14px;
  }
  .btn-show {
    padding: 16px 20px;
    font-size: 14px;
    width: 100%;
  }
}
.networks {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.network {
  border-radius: 20px;
  padding: 8px;
  background: #e9f1ef;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.3;
  text-transform: uppercase;
  color: var(--tekst-osnovnoy-tekst);
  max-width: 360px;
  transition: all 0.3s ease;
}
.network-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  background: var(--belyy);
  width: 60px;
  aspect-ratio: 1/1;
  flex-shrink: 0;
}
.network-name {
  flex-grow: 1;
}
.network-num {
  border: 1px solid rgba(26, 93, 90, 0.2);
  border-radius: 10px;
  padding: 21px 9px;
  font-size: 14px;
  color: var(--osnovnye-kdk-k-zelenyy);
}
.network:hover {
  background: var(--osnovnye-kdk-k-zelenyy);
  color: var(--belyy);
}
.network:hover .network-num {
  border-color: rgba(255, 255, 255, 0.2);
  color: var(--belyy);
}
.network:active {
  background: rgba(26, 93, 90, 0.9);
}

.network.light {
  background: rgba(255, 255, 255, 0.15);
  color: var(--belyy);
}
.network.light .network-num {
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: var(--belyy);
}
.network.light:hover {
  background: var(--osnovnye-kdk-k-zelenyy);
}

@media (max-width: 768px) {
  .networks {
    gap: 8px;
  }
  .network {
    border-radius: 15px;
    gap: 12px;
    font-size: 16px;
    max-width: 100%;
  }
  .network-icon {
    border-radius: 7px;
    width: 48px;
  }
  .network-num {
    border-radius: 7px;
    padding: 16px 8px;
    font-size: 12px;
  }
}
/*  form  */
input,
textarea {
  border-radius: 10px;
  background: var(--belyy);
  padding: 20px 24px;
  font-size: 18px;
  color: var(--tekst-osnovnoy-tekst);
  border: 1px solid transparent;
  transition: all 0.3s ease;
}
input::placeholder,
textarea::placeholder {
  color: #686d79;
}
input:hover, input:focus, input:active,
textarea:hover,
textarea:focus,
textarea:active {
  border: 1px solid rgba(26, 93, 90, 0.2);
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

textarea {
  width: 100%;
  min-height: 142px;
  resize: none;
}

select {
  border-radius: 10px;
  background: var(--belyy) url("./../img/icons/select-arrow.svg") no-repeat top 8px right 8px/47px;
  padding: 20px 24px;
  padding-right: 60px;
  font-size: 18px;
  color: var(--tekst-osnovnoy-tekst);
  color: #686d79;
  border: 1px solid transparent;
  outline: none;
  transition: all 0.3s ease;
  position: relative;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}
select option {
  color: #686d79;
}
select:focus {
  background-image: url("./../img/icons/select-arrow-open.svg");
}

.politic {
  display: flex;
  align-items: start;
  gap: 10px;
  line-height: 1.2;
  color: #30423c;
}
.politic a {
  text-decoration: underline;
  color: var(--osnovnye-kdk-k-zelenyy);
  transition: all 0.3s ease;
}
.politic a:hover {
  opacity: 0.7;
}
.politic input[type=checkbox] {
  position: relative;
  width: 24px;
  height: 24px;
  color: #000;
  border: 2px solid var(--osnovnye-kdk-k-zelenyy);
  border-radius: 6px;
  appearance: none;
  cursor: pointer;
  transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
  background: transparent;
  flex-shrink: 0;
}
.politic input[type=checkbox]::before {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 10px;
  border-style: solid;
  border-color: var(--belyy);
  border-width: 0 2px 2px 0;
  transform: translate(-50%, -65%) rotate(45deg);
  opacity: 0;
}
.politic input[type=checkbox]:hover {
  background: rgba(26, 93, 90, 0.2);
}
.politic input[type=checkbox]:checked {
  background: var(--osnovnye-kdk-k-zelenyy);
}
.politic input[type=checkbox]:checked::before {
  opacity: 1;
}

.resume {
  display: flex;
  align-items: center;
  gap: 16px;
  margin: 25px 0;
}
.resume:hover .resume-btn path {
  fill-opacity: 1;
}
.resume-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  padding: 0;
  position: relative;
  flex-shrink: 0;
}
.resume-btn svg path {
  transition: all 0.3s ease;
}
.resume input[type=file] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.resume label {
  display: block;
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  color: #30423c;
  margin-bottom: 4px;
}
.resume span {
  font-size: 16px;
  line-height: 130%;
  color: #8f9c96;
}
.resume .file-item {
  display: flex;
  align-items: center;
  gap: 8px;
}
.resume .file-remove {
  cursor: pointer;
}

form.orange input,
form.orange textarea {
  background: #e15c32;
  color: var(--belyy);
}
form.orange input::placeholder,
form.orange textarea::placeholder {
  color: var(--belyy);
}
form.orange input:hover, form.orange input:focus, form.orange input:active,
form.orange textarea:hover,
form.orange textarea:focus,
form.orange textarea:active {
  border: 1px solid rgba(255, 255, 255, 0.5);
}
form.orange .politic {
  color: var(--belyy);
}
