/* swiper CUSTOM */
.swiper {
  width: 100%;
  height: 100%;
}

/* swiper-buttons */
.swiper-btns {
  display: flex;
  align-items: center;
  gap: 8px;
}

.swiper-button-next, .swiper-button-prev {
  position: static;
  margin: 0;
  width: 69px;
  height: 50px;
  border-radius: 10px;
  border: 1px solid #c8d3cf;
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.swiper-button-next:after, .swiper-button-prev:after {
  color: #676c6a;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
}

.swiper-button-next:hover, .swiper-button-prev:hover {
  border: 1px solid var(--osnovnye-kdk-k-zelenyy);
}

