
// job
.job {
    &__swiper-slide {
        height: auto;
    }

    &__card {
        height: 100%;
        border-radius: 20px;
        padding: 32px;
        background: #e9f2ef;
    }

    &__card-pic {
        display: block;
        width: 100%;
        margin-bottom: 24px;
        & img {
            width: 100%;
            height: 220px;
            object-fit: cover;
            border-radius: 10px;
        }
    }

    &__card-title {
        font-weight: 500;
        font-size: 36px;
        line-height: 1;
        color: #30423c;
        margin-bottom: 16px;
    }

    &__card-text {
        opacity: 0.8;
    }
}

@media (max-width: 1500px) {
    .job {
        &__card {
            padding: 28px;
        }
    
        &__card-pic {
            margin-bottom: 24px;
            & img {
                height: 220px;
                border-radius: 10px;
            }
        }
    
        &__card-title {
            font-size: 30px;
            margin-bottom: 16px;
        }
    }
}
@media (max-width: 768px) {
    .job {
        &__card {
            border-radius: 15px;
            padding: 24px;
        }
    
        &__card-pic {
            margin-bottom: 16px;
            & img {
                height: 140px;
                border-radius: 5px;
            }
        }
    
        &__card-title {
            font-size: 24px;
            margin-bottom: 12px;
        }
    }
}

