// contacts
.contacts {
	&__row {
		display: flex;
		justify-content: space-between;
		gap: 50px;
	}

	&__items {
		display: flex;
		flex-direction: column;
		gap: 32px;
		margin-bottom: 32px;
	}

	&__item {
		& span {
			display: block;
			color: #8f9c96;
			margin-bottom: 12px;
		}

		& a {
			font-weight: 500;
			font-size: 30px;
			line-height: 1.2;
			text-align: right;
			color: #30423c;
			display: flex;
			align-items: center;
			gap: 14px;

			&:hover {
				text-decoration: underline;
			}

			&::before {
				content: "";
				display: block;
				width: 10px;
				height: 10px;
				border-radius: 100%;
				background: #f04a25;
			}
		}
	}

	&__networks-text {
		display: none;
	}

	&__map {
		border-radius: 20px;
		width: 100%;
		max-width: 1042px;
		background: #e9f1ef;
		position: relative;
		overflow: hidden;
		&-pic {
			display: block;
			width: 1600px;
			& img {
				width: 100%;
				height: 100%;
				max-height: 446px;
				object-fit: cover;
				object-position: left bottom;
			}
		}
	}

	&__dots {
		position: relative;
		height: 100%;
		width: 1600px;
		margin-top: -446px;
		min-height: 446px;
		max-height: 446px;
	}
	&__dot {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 14px;
		height: 14px;
		border-radius: 50%;
		background: #1a5d5a;
		outline: 20px solid rgba(26, 93, 90, 0.1);
		position: relative;
		z-index: 5;
		font-size: 12px;
		line-height: 120%;
		color: var(--belyy);
		flex-shrink: 0;
		transition: all 0.3s ease;
		position: absolute;
		& span {
			opacity: 0;
			pointer-events: none;
			transition: all 0.3s ease 0.2s;
		}
		&:nth-child(1) {
			left: 14%;
			bottom: 39%;
		}
		&:nth-child(2) {
			left: 13%;
			bottom: 49%;
		}
		&:nth-child(3) {
			left: 11%;
			bottom: 34%;
		}
		&:nth-child(4) {
			left: 14%;
			bottom: 90%;
		}

		&.tab-btn--active {
			width: 97px;
			height: 97px;
			background: var(--osnovnye-kdk-osnovnoy-500);
			transform: translate(-36px, 36px);
			outline: none;
			z-index: 7;
			& span {
				opacity: 1;
			}
			&::after {
				content: "";
				display: block;
				width: 123px;
				height: 123px;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				border-radius: 100%;
				border: 1px solid rgba(240, 74, 37, 0.5);
			}
		}
	}
}

// media contacts
@media (max-width: 1024px) {
	.contacts {
		&__row {
			flex-direction: column;
			gap: 40px;
		}
	}
}
@media (max-width: 768px) {
	.contacts {
		&__items {
			gap: 24px;
			margin-bottom: 24px;
		}

		&__item {
			& span {
				margin-bottom: 10px;
			}

			& a {
				font-size: 24px;
				gap: 10px;
			}
		}

		&__networks-text {
			display: block;
			font-weight: 500;
			font-size: 14px;
			line-height: 130%;
			text-transform: uppercase;
			color: #8f9c96;
			margin-bottom: 10px;
		}

		&__map {
			border-radius: 15px;
			&-pic {
				width: 1400px;
				margin-left: -40px;
			}
		}

		&__dots {
			width: 1400px;
			margin-left: -40px;
		}
		&__dot {
			&:nth-child(1) {
				left: 14%;
				bottom: 29%;
			}
			&:nth-child(2) {
				left: 13%;
				bottom: 39%;
			}
			&:nth-child(3) {
				left: 11%;
				bottom: 24%;
			}
			&:nth-child(4) {
				left: 14%;
				bottom: 80%;
			}
		}
	}
}
