
// footer
.footer {
    padding: 80px 0;
    background: var(--osnovnye-kdk-k-zelenyy);

    &_logo {
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    &__nav {
        flex-grow: 1;
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        flex-grow: 2;
    }

    &__policy {
        display: flex;
        flex-direction: column;
        gap: 7px;
        & p {
            font-size: 16px;
            line-height: 1.2;
            color: var(--osnovnye-kdk-k-svetlo-zelenyy);
        }
        & a {
            line-height: 1.3;
            text-decoration: underline;
            text-decoration-skip-ink: none;
            color: var(--belyy);
        }
    }

    &__col-title {
        font-size: 16px;
        line-height: 1.2;
        color: var(--osnovnye-kdk-k-svetlo-zelenyy);
        margin-bottom: 32px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 16px;
        & a {
            font-weight: 500;
            font-size: 24px;
            line-height: 1.2;
            color: var(--belyy);
            transition: all 0.3s ease; 
            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__contact {
        display: flex;
        align-items: center;
        gap: 10px;
        &::before {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            border-radius: 100%;
            background: #F04A25;
        }
    }
}


@media (max-width: 1024px) {
    .footer {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
    
        &__col, &__logo {
            padding-bottom: 24px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        }
    
        &__info {
            display: contents;
        }

        &__nav {
            order: 2;
        }
    
        &__policy {
            order: 3;
        }
    }
}
@media (max-width: 768px) {
    .footer {
        padding: 80px 0 30px;

        &__row {
            gap: 24px;
        }
    
        &__policy {
            & p {
                font-size: 14px;
            }
            & a {
                font-size: 16px;
            }
        }
    
        &__col-title {
            font-size: 14px;
            margin-bottom: 16px;
        }
    
        &__list {
            gap: 12px;
            & a {
                font-size: 18px;
            }
        }
    }
}